@import url('https://fonts.googleapis.com/css?family=Roboto');


/* Global Styles */
:root {
  --primary-color: #5c6bc0;
  --dark-color: #2c2d2f;
  --light-color: #f4f4f4;
  --danger-color: #dc3545;
  --success-color: #28a745;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;

}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  background-image: linear-gradient(to right, #2c2d2f, #272727,  #282828);
  color: #333;
}

a {
  color: var(--primary-color);
  text-decoration: none;
}

a:hover {
  color: #666;
}

ul {
  list-style: none;
}

img {
  width: 100%;
}

/* Utilities */
.container {
  max-width: 1100px;
  margin: auto;
  overflow: hidden;
  padding: 0 2rem;
}

/* Text Styles*/
.x-large {
  font-size: 4rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.large {
  font-size: 3rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.lead {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.text-center {
  text-align: center;
}

.text-primary {
  color: var(--primary-color);
}

.text-dark {
  color: var(--dark-color);
}

.text-success {
  color: var(--success-color);
}

.text-danger {
  color: var(--danger-color);
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

/* Center All */
.all-center {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: auto;
  justify-content: center;
  align-items: center;
  text-align: center;
}

/* Cards */
.card {
  padding: 1rem;
  border: #ccc 1px dotted;
  margin: 0.7rem 0;
}

/* List */
.list {
  margin: 0.5rem 0;
}

.list li {
  padding-bottom: 0.3rem;
}

/* Padding */
.p {
  padding: 0.5rem;
}
.p-1 {
  padding: 1rem;
}
.p-2 {
  padding: 2rem;
}
.p-3 {
  padding: 3rem;
}
.py {
  padding: 0.5rem 0;
}
.py-1 {
  padding: 1rem 0;
}
.py-2 {
  padding: 2rem 0;
}
.py-3 {
  padding: 3rem 0;
}

/* Margin */
.m {
  margin: 0.5rem;
}
.m-1 {
  margin: 1rem;
}
.m-2 {
  margin: 2rem;
}
.m-3 {
  margin: 3rem;
}
.my {
  margin: 0.5rem 0;
}
.my-1 {
  margin: 1rem 0;
}
.my-2 {
  margin: 2rem 0;
}
.my-3 {
  margin: 3rem 0;
}

/* Grid */
.grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
}

.grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
}

.grid-4 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1rem;
}
.grid-12 {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 1rem;
}

.btn {
  display: inline-block;
  background: var(--light-color);
  color: #333;
  padding: 0.4rem 1.3rem;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  margin-right: 0.5rem;
  transition: opacity 0.2s ease-in;
  outline: none;
}

.btn-link {
  background: none;
  padding: 0;
  margin: 0;
}

.btn-block {
  display: block;
  width: 100%;
}

.btn-sm {
  font-size: 0.8rem;
  padding: 0.3rem 1rem;
  margin-right: 0.2rem;
}

.badge {
  display: inline-block;
  font-size: 0.8rem;
  padding: 0.2rem 0.7rem;
  text-align: center;
  margin: 0.3rem;
  background: var(--light-color);
  color: #333;
  border-radius: 5px;
}

.alert {
  padding: 0.7rem;
  margin: 1rem 0;
  opacity: 0.9;
  background: var(--light-color);
  color: #333;
}

.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
  background: var(--primary-color);
  color: #fff;
}

.btn-light,
.bg-light,
.badge-light,
.alert-light {
  background: var(--light-color);
  color: #333;
}

.btn-dark,
.bg-dark,
.badge-dark,
.alert-dark {
  background: var(--dark-color);
  color: #fff;
}

.btn-danger,
.bg-danger,
.badge-danger,
.alert-danger {
  background: var(--danger-color);
  color: #fff;
}

.btn-success,
.bg-success,
.badge-success,
.alert-success {
  background: var(--success-color);
  color: #fff;
}

.btn-white,
.bg-white,
.badge-white,
.alert-white {
  background: #fff;
  color: #333;
  border: #ccc solid 1px;
}

.btn:hover {
  opacity: 0.8;
}

.bg-light,
.badge-light {
  border: #ccc solid 1px;
}

.round-img {
  border-radius: 50%;
}

/* Forms */
input {
  margin: 1.2rem 0;
}

.form-text {
  display: block;
  margin-top: 0.3rem;
  color: #888;
}

input[type='text'],
input[type='email'],
input[type='password'],
input[type='date'],
select,
textarea {
  display: block;
  width: 100%;
  padding: 0.4rem;
  font-size: 1.2rem;
  border: 1px solid #ccc;
}

input[type='submit'],
button {
  font: inherit;
}

table th,
table td {
  padding: 1rem;
  text-align: left;
}

table th {
  background: var(--light-color);
}

/* Navbar */
.navbar {
  
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.7rem 2rem;
  padding-left: 5%;
  padding-right: 5%;
  z-index: 1;
  width: 100%;
  opacity: 0.9;
  margin-bottom: 1rem;
}

.navbar h1 {
  color: white;
  font-weight: bolder;
}

.navbar ul {
  display: flex;
}

.navbar a {
  color: #fff;
  padding: 0.45rem;
  margin: 0 0.25rem;
  font-weight: bold;
}

.navbar a:hover {
  color: var(--light-color);
}

.navbar .welcome span {
  margin-right: 0.6rem;
}

/* Mobile Styles */
@media (max-width: 700px) {
  .hide-sm {
    display: none;
  }

  .grid-2,
  .grid-3,
  .grid-4 {
    grid-template-columns: 1fr;
  }

  /* Text Styles */
  .x-large {
    font-size: 3rem;
  }

  .large {
    font-size: 2rem;
  }

  .lead {
    font-size: 1rem;
  }

  /* Navbar */
  .navbar {
    display: block;
    text-align: center;
  }

  .navbar ul {
    text-align: center;
    justify-content: center;
  }
}

/* Introduction Container */
.intro-container {
  margin-top: 10%;
  animation-name: fadeInAndFromLeft;
  animation-duration: 1.5s;
  height: 100%;
  
}

.intro-name {
  font-size: 10vh;
  color: #fdd835;
}

.intro-title {
  font-size: 5vh;
  color: #ffecb3;
}

.intro-btn{
  margin-top: 10%;
  width: 20%;
  height: 10%;
}

/* About */
.about {
  
  
  padding-left: 5%;
  padding-right: 5%;
  margin-top: 20%;
  padding-top: 3%;
  background-color: white;
}

.about-heading {
  font-size: 8vh;
  font-weight: bold;
}

.about-img {
  margin-top: 5%;
  width: 400px;
  height: 400px;
}

.about-desc {
  text-align: left  ;
  margin-top: 5%;
  font-size: 3.5vh;
}

/* Skills */
.skills {

  padding-left: 5%;
  padding-right: 5%;
  padding-top: 10%;
  background-color: white;
}

.skills-desc {
  text-align: left;
  margin-top: 5%;
  font-size: 3.5vh;
}

.skill-items {
  margin-top: 5%;
}

.skills-heading {
  font-size: 8vh;
  font-weight: bold;
}

/* Projects */
.project {
  padding-left: 5%;
  padding-right: 5%;
  padding-top: 5%;
  
  background-color: white;
}

.project-img {
  width: 200px;
  height: 200px; 
}

.project-desc {
  padding-top: 3%;
}
.project-heading {
  font-size: 8vh;
  font-weight: bold;
  text-align: center;
  
}

.project-item {
  margin-top: 5%;
  padding: 20px;
  border: none;
  border-radius: 2%;
}

/* Services */
.services {
  padding-top: 5%;
  background-color: white;
  padding-right: 5%;
  padding-left: 5%;
}

.service {
  border: rgba(48, 47, 47, 0.5) 2px solid;
  padding: 10px;
  height: 50vh;
  border-radius: 5%;
  margin-bottom: 5%;
}


.service-heading {
  font-size: 8vh;
  font-weight: bold;
  padding-bottom: 5%;
}

.service-img {
  width: 100px;
  height: 100px;
}

/* Contact */
.contact {
  background-color: white;
  padding-top: 5%;
  padding-left: 5%;
  padding-left: 5%;
}

.contact-heading {
  font-size: 8vh;
}

.footer {
  padding-top: 2%;
  height: 50vh;
  padding-left: 5%;
  padding-right: 5%;
}

.footer-contact {
  font-size: 3vh;
  color: var(--primary-color);

}

.footer-heading {
  font-size: 5vh;
  padding-bottom: 4%;
  color: white;

}


/* Media Queries */

@media only screen and (max-width: 600px) {
  .intro-container {
    animation-name: none;
    margin-top: 10%;
    
  }

  .intro-btn {
    margin-top: 25%;
    width: 35%;
  }

  .about-img {
    width: 350px;
    height: 350px;
    
  }
}

/* Animations */
@keyframes fadeInAndFromLeft {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }

  25% {
    transform: translateX(-75%);
    opacity: 25%;
  }

  50% {
    transform: translateX(+10%);
    opacity: 50%;
  }

  75% {
    opacity: 75%;
  }

  100% {
    opacity: 1;
  }
}